import cookie from 'react-cookie'
// import Vue from 'vue'
import { CookieDomain } from './config.js'
let cookieConfig = {}
if (CookieDomain !== '') {
  cookieConfig = { domain: CookieDomain }
}

export function saveCookie (name, value, cookieConfig) {
  cookie.save(name, value, cookieConfig)
}

export function getCookie (name) {
  return cookie.load(name)
}

export function removeCookie (name) {
  cookie.remove(name, cookieConfig)
}

export function signOut () {
  window.sessionStorage.clear()
  window.location.href = '/logout'
}

export function isLogin () {
  return !!cookie.load('wemesh')
  // let wemeshToken = cookie.load('wemesh_token')
  // Vue.$http.post('auth/login', wemeshToken).then((response) => {
  //   window.console.log(response.status)
  // })
}



